/* IMAGE FORMATS */

.image001 {
  width: 50% !important;
  padding: 8px;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 20px;
}

@media (max-width: 540px) {
  .image001 {
    width: 100% !important;
  }
}

.image002 {
  width: 40% !important;
  padding: 8px;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 20px;
}

.image003 {
  width: 30% !important;
  padding: 8px;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 20px;
}

.center {
  margin-right: auto;
  margin-left: auto;
}

.tag {
  background: rgb(140, 140, 236);
  color: white;
  font-weight: 500;
  border-radius: 50px;
  padding-left: 12px;
  padding-right: 12px;
  margin-right: 8px;
  margin-bottom:10px;
}

.tagRow {
